.za-wheel {
  display: block;
  position: relative;
  overflow: hidden;
  height: 170px;
  width: 100%; }
  .za-wheel-content {
    margin-top: 68px; }
  .za-wheel-item {
    text-align: center;
    font-size: 18px;
    line-height: 34px;
    height: 34px;
    color: #000;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; }

.za-picker-panel {
  height: 210px;
  position: relative;
  z-index: 600;
  bottom: 0;
  width: 100%;
  background: #fff;
  padding: 20px 0; }

.za-picker-mask-top, .za-picker-mask-bottom {
  z-index: 10;
  width: 100%;
  height: 88px;
  pointer-events: none;
  -webkit-transform: translateZ(0);
          transform: translateZ(0); }

.za-picker-mask-top {
  position: absolute;
  top: 0;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0.4)), to(rgba(255, 255, 255, 0.8)));
  background: linear-gradient(to top, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.8)); }
  .za-picker-mask-top:after {
    content: "";
    display: block;
    position: absolute;
    border-bottom: 1px solid #ebebeb;
    left: 0;
    bottom: 0;
    width: 100%;
    -webkit-transform-origin: 0 bottom;
    transform-origin: 0 bottom; }

.za-picker-mask-bottom {
  position: absolute;
  bottom: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.4)), to(rgba(255, 255, 255, 0.8)));
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.8)); }
  .za-picker-mask-bottom:before {
    content: "";
    display: block;
    position: absolute;
    border-top: 1px solid #ebebeb;
    left: 0;
    top: 0;
    width: 100%;
    -webkit-transform-origin: 0 top;
    transform-origin: 0 top; }

.za-picker-view {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
